.container {
  position: relative;
  user-select: none;
}

.profileBtn {
  vertical-align: middle;
  height: 45px;
  width: 45px;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  background-color: transparent;
  overflow: hidden;
  border: 2px solid var(--action);
}

.profileBtn img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
